// /lib/renderLanding.ts
import { SECTION_MAP } from "./sectionMap";
import { removeDuplicates, filterEnabled } from "./sectionUtils";
import * as CARDS from "@/templates/cards";

type Product = any;

// [Your existing applyTemplate, mapJsonToProduct, convertToEmbedUrl remain unchanged]
// ... keep those functions exactly as they are ...

export function renderLanding(raw: any, templateHtml?: string, rawDataBase64?: string): string {
  const product = mapJsonToProduct(raw);
  let output = templateHtml || ""; // fallback if no template

  // Decode fullData from base64 (contains sections order + styles)
  let fullData: any = null;
  if (rawDataBase64) {
    try {
      fullData = JSON.parse(Buffer.from(rawDataBase64, "base64").toString("utf-8"));
    } catch (e) {
      console.error("Invalid Base64 JSON:", e);
    }
  }

  // Build dynamic data object for template replacement
  const dynamicValues = {
    ...product,
    images: product.images || [],
    thumbnails: product.thumbnails || [],
    custom_fields_meta: product.custom_fields_meta || [],
    videoEmbedUrl: convertToEmbedUrl(product.videoUrl || ""),
    ...(fullData?.styles || {})
  };

  // === 1. Inject Global CSS ===
  const globalCssInjected = applyTemplate(CARDS.GLOBAL_CSS, {
    primaryColor: (fullData?.styles?.primaryColor || "#061244").replace("#", ""),
    backgroundColor: (fullData?.styles?.backgroundColor || "#ffffff").replace("#", ""),
    textColor: (fullData?.styles?.textColor || "#000000").replace("#", ""),
    hsize: fullData?.styles?.headlineSize || 28,
    psize: fullData?.styles?.paragraphSize || 16
  });
  output = output.replace("{{global_css}}", globalCssInjected);

  // === 2. Simple field replacements (pageTitle, etc.) ===
  const simpleFields = [
    "pageTitle", "metaDescription", "metaKeywords", "brandLogo", "productName",
    "price", "description", "videoUrl", "category", "pdfDownloadUrl",
    "pdfButtonText", "og_image", "productId"
  ];
  simpleFields.forEach(k => {
    const val = (product as any)[k] ?? (dynamicValues as any)[k] ?? "";
    output = output.replaceAll(`{{${k}}}`, String(val));
  });

  // === 3. DYNAMIC SECTION RENDERING WITH CORRECT ORDER ===
  if (fullData && Array.isArray(fullData.sections)) {
    const enabledSections = fullData.sections
      .filter((s: any) => s.enabled === true) // Only enabled
      .map((s: any) => s.section); // Keep original order!

    let renderedSectionsHtml = "";

    for (const sectionName of enabledSections) {
      const placeholder = SECTION_MAP[sectionName];
      if (!placeholder) continue;

      let cardHtml = "";

      // Map section name → correct card template
      switch (placeholder) {
        case "{{meta_seo}}":
          cardHtml = applyTemplate(CARDS.Meta_SEO_CARD, dynamicValues);
          break;
        case "{{header_logo_card}}":
          cardHtml = applyTemplate(CARDS.HEADER_LOGO_CARD, dynamicValues);
          break;
        case "{{header_logo_card_tow}}":
          cardHtml = applyTemplate(CARDS.HEADER_LOGO_CARD_TOW, dynamicValues);
          break;
        case "{{top_slider_card}}":
          cardHtml = applyTemplate(CARDS.TOP_SLIDER_CARD, dynamicValues);
          break;
        case "{{top_slider_card_tow}}":
          cardHtml = applyTemplate(CARDS.TOP_SLIDER_CARD_TOW, dynamicValues);
          break;
        case "{{about_brand}}":
          cardHtml = applyTemplate(CARDS.ABOUT_BRAND_CARD, dynamicValues);
          break;
        case "{{product_spotlight}}":
          cardHtml = applyTemplate(CARDS.PRODUCT_SPOTLIGHT_CARD, dynamicValues);
          break;
        case "{{specifications}}":
          cardHtml = applyTemplate(CARDS.SPECS_CARD, dynamicValues);
          break;
        case "{{our_collection}}":
          cardHtml = applyTemplate(CARDS.OUR_COLLECTION_CARD, dynamicValues);
          break;
        case "{{youtube}}":
          cardHtml = applyTemplate(CARDS.YOUTUBE_CARD, dynamicValues);
          break;
        case "{{featured_products}}":
          cardHtml = applyTemplate(CARDS.FEATURED_PRODUCTS_CARD, dynamicValues);
          break;
        case "{{join_club}}":
          cardHtml = applyTemplate(CARDS.JOIN_BEAUTY_CLUB_CARD, dynamicValues);
          break;
        case "{{natural_ingredients}}":
          cardHtml = applyTemplate(CARDS.NATURAL_INGREDIENTS_CARD, dynamicValues);
          break;
        case "{{social_media}}":
          cardHtml = applyTemplate(CARDS.SOCIAL_MEDIA_CARD, dynamicValues);
          break;
        case "{{contact}}":
          cardHtml = applyTemplate(CARDS.CONTACT_CARD, dynamicValues);
          break;
        case "{{rate_experience}}":
          cardHtml = applyTemplate(CARDS.RATE_EXPERIENCE_CARD, dynamicValues);
          break;
        default:
          continue; // skip unknown
      }

      renderedSectionsHtml += cardHtml + "\n";
    }

    // === CRITICAL: Replace ALL section placeholders in one go with dynamic content ===
    // First remove all old placeholders
    output = output.replace(/{{meta_seo}}[\s\S]*?{{rate_experience}}/g, ""); // rough clean (optional)
    
    // Better: Remove all known section placeholders first
    const allPlaceholders = Object.values(SECTION_MAP).filter(p => typeof p === "string");
    allPlaceholders.forEach(ph => {
      output = output.replace(new RegExp(ph.replace(/[{}]/g, "\\$&"), "g"), "");
    });

    // Now inject all rendered sections in correct order at a single insertion point
    // You must have ONE anchor in your template where sections go!
    output = output.replace("{{dynamic_sections}}", renderedSectionsHtml.trim());
  }

  // Final cleanup: remove any leftover {{something}} (safety)
  output = applyTemplate(output, dynamicValues);

  return output;
}